<template>
  <button class="firefly">
    <p>萤火虫</p>
    <div class="lightning"></div>
  </button>
</template>

<script>
import $ from "jquery";
export default {
  name: "Lightbutton",
};
var lgh = $(".lightning li").length;
console.log(lgh);

$(".lightning li").each(function (i) {
  $(this).css({
    left: i * (100 / lgh) + "%",
    bottom: randomNum(-20, 10) + "%",
    animationDuration: randomNum(1, 5) + "s",
  });
});

function randomNum(max, min) {
  var num = Math.floor(Math.random() * (max - min + 1) + min);
  return num;
}
</script>

<style>
@font-face {
  font-family: "firefly";
  src: "@/assets/ZCOOLKuaiLe-Regular.ttf";
}
* {
  padding: 0;
  margin: 0;
}
body {
  height: 100vh;
  background-size: cover;
}
ul {
  list-style: none;
}
button {
  outline: none;
  border: none;
}
.firefly {
  width: 180px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: linear-gradient(to right, #6eb46e 10%, #55b455);
  border-radius: 40px;
  opacity: 0.88;
  cursor: pointer;
  transition: 1s;
}
.firefly:hover {
  box-shadow: 0 0 10px #b4ffb4;
}
.firefly p {
  line-height: 60px;
  font-size: 22px;
  color: #f5dd8f;
  font-family: firefly;
  opacity: 0.88;
}
.lightning {
  width: 95%;
  height: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 40px;
  transition: 0.8s;
  overflow: hidden;
}
.firefly:hover .lightning {
  box-shadow: 0 0 4px #b4ffb4 inset;
}
.lightning ul {
  opacity: 0;
  transition: 0.8s;
}
.firefly:hover ul {
  opacity: 0.8;
}
.lightning ul li {
  width: 5px;
  height: 5px;
  background-color: #91fa91;
  position: absolute;
  bottom: 10%;
  border-radius: 50%;
  opacity: 0.6;
  animation: fireflymove infinite linear;
}
@keyframes fireflymove {
  100% {
    bottom: 100%;
  }
}
</style>


